import i18n from 'i18next';
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Navigate } from 'react-router-dom';

import Page from '../../components/app/Page';
import { IGroupModel } from '../../models/groupModel';
import { ISchoolModel } from '../../models/schoolModel';
import useNetwork from '../../utils/network/Network';
import { ROUTE_GROUP, ROUTE_GROUP_DETAIL } from '../../utils/routes/Routes';
import useToken from '../../utils/useToken';
import CreateGrouptModal, { ICreateGroupModalRefs } from './modals/CreateGroupModal';

export default function Group() {
    useEffect(() => {
        async function getData() {
            let groupList = await fetchGroups();
            if (groupList.status === 200) {
                const result = await groupList.json().then((json) => JSON.stringify(json));
                setGroups(JSON.parse(result));
            } else {
                (document.querySelector('.dashboard-students') as HTMLDivElement).style.display =
                    'block';
            }

            let schoolList = await fetchSchools();
            if (schoolList.status === 200) {
                const result = await schoolList.json().then((json) => JSON.stringify(json));
                setSchools(JSON.parse(result));
            }
        }
        getData();
    }, [i18n.language]);

    const { token } = useToken();
    const { t } = useTranslation();
    const [groups, setGroups] = useState<IGroupModel[] | undefined>(undefined);
    const [schools, setSchools] = useState<ISchoolModel[] | undefined>(undefined);
    const { fetchGroups, fetchSchools } = useNetwork();

    const ref = React.createRef<ICreateGroupModalRefs>();

    if (!token) {
        return (
            <>
                <Navigate replace to="/login" />
            </>
        );
    }

    const groupList = groups?.map((x: IGroupModel) => (
        <div key={x.id} className="list-link">
            <a
                style={{ color: '#'.concat(x.color) }}
                className={'animate__animated animate__bounceInRight'}
                href={ROUTE_GROUP_DETAIL + x.id}
            >
                {x.name + ' - ' + x.year}
            </a>
            <hr />
        </div>
    ));

    const component = (
        <div className="list">
            <h1 className="text-center list-header">{t('group_title')}</h1>
            <div className="list-container">
                <div className="list-collapsible">
                    {
                        groupList?.length ?
                            groupList : (
                                <div className="text-center">
                                    {t('group_no_results')}
                                </div>
                            )
                    }
                </div>
            </div>

            <button onClick={() => ref.current?.openModal()} className="auth-form-btn list-add">
                {t('group_add')}
            </button>
            {/* eslint-disable-next-line react/jsx-no-undef */}
            <CreateGrouptModal schools={schools ?? []} ref={ref} />
        </div>
    );

    return (
        <>
            <Page menu={ROUTE_GROUP} component={component} />
        </>
    );
}
