import { EntityRefModel, IEntityRefModel } from './entityRefModel';
import { ISchoolModel } from './schoolModel';

export interface IGroupModel extends IEntityRefModel {
    name: string;
    year: number;
    color: string;
    description: string;
    school: ISchoolModel | IEntityRefModel;
}

export class GroupModel extends EntityRefModel implements IGroupModel {
    name: string;
    year: number;
    color: string;
    description: string;
    school: ISchoolModel | IEntityRefModel;

    constructor(props: IGroupModel) {
        super(props);
        this.name = props.name;
        this.year = props.year;
        this.color = props.color;
        this.description = props.description;
        this.school = props.school;
    }

    toJson() {
        const obj = {
            ...this,
            school: this.school.id,
        };
        return JSON.stringify(obj);
    }
}
