import i18n from 'i18next';
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Navigate, useNavigate } from 'react-router-dom';

import Page from '../../components/app/Page';
import { IGroupModel } from '../../models/groupModel';
import { INoteModel, NoteModel, TargetModel } from "../../models/noteModel";
import { ISchoolModel } from '../../models/schoolModel';
import { IStudentModel } from '../../models/studentModel';
import useNetwork from '../../utils/network/Network';
import {
    ROUTE_DASHBOARD,
    ROUTE_GROUP,
    ROUTE_GROUP_DETAIL,
    ROUTE_STUDENT,
    ROUTE_STUDENT_DETAIL,
} from '../../utils/routes/Routes';
import useToken from '../../utils/useToken';
import CreateNoteModal, { ICreateNoteModalProps } from '../student/modals/CreateNoteModal';

export default function Dashboard() {
    const [groups, setGroups] = useState<IGroupModel[] | undefined>(undefined);
    const [students, setStudents] = useState<IStudentModel[] | undefined>(undefined);
    const [notes, setNotes] = useState<INoteModel[] | undefined>(undefined);
    const navigate = useNavigate();

    useEffect(() => {
        async function getData() {
            let groupsList = await fetchGroups();
            if (groupsList.status == 200) {
                const result = await groupsList.json().then((json) => JSON.stringify(json));
                setGroups(JSON.parse(result));
            } else {
                (document.querySelector('.dashboard-groups') as HTMLDivElement).style.display =
                    'block';
            }

            let studentList = await fetchStudents();
            if (studentList.status == 200) {
                const result = await studentList.json().then((json) => JSON.stringify(json));
                setStudents(JSON.parse(result));
            } else {
                (document.querySelector('.dashboard-students') as HTMLDivElement).style.display =
                    'block';
            }

            let noteList = await fetchNotes();
            if (noteList.status == 200) {
                const result = await noteList.json().then((json) => JSON.stringify(json));
                setNotes(JSON.parse(result));
            } else {
                (document.querySelector('.dashboard-notes-error') as HTMLDivElement).style.display =
                    'block';
            }
        }
        getData();
    }, [i18n.language]);


    const { t } = useTranslation();
    const { fetchGroups, fetchStudents, fetchNotes } = useNetwork();
    const ref = React.createRef<ICreateNoteModalProps>();
    const { token } = useToken();
    if (!token) {
        return (
            <>
                <Navigate replace to="/login" />
            </>
        );
    }

    const groupCount = groups?.length;
    groups?.sort(function (a, b) {
        return b.year - a.year;
    });
    const groupItem = groups?.slice(0, 4).map((group) => (
        <tr onClick={() => navigate(ROUTE_GROUP_DETAIL + group.id)} key={group.id}>
            <td>{group.name}</td>
            <td>{group.year}</td>
            <td>{(group.school as ISchoolModel).name}</td>
        </tr>
    ));

    const studentCount = students?.length;
    students?.sort(function (a, b) {
        return b.year - a.year;
    });

    const studentItem = students?.slice(0, 4).map((student) => (
        <tr onClick={() => navigate(ROUTE_STUDENT_DETAIL + student.id)} key={student.id}>
            <td>
                {student.first_name} {student.last_name}
            </td>
            <td>{t('year_' + student.year)}</td>
            <td>{(student.group as IGroupModel[]).map((x) => x.name).join(', ')}</td>
        </tr>
    ));

    const noteCount = notes?.length;
    notes?.sort(function (a, b) {
        return new Date(b.date).getTime() - new Date(a.date).getTime();
    });

    const noteItem = notes?.slice(0, 4).map((note: INoteModel) => {
        const student = note.student as IStudentModel;
        return (
            <tr onClick={() => ref.current?.openModal(note, note.student)} key={note.title}>
                <td>
                    {student?.first_name} {student?.last_name}
                </td>
                <td>{note.title}</td>
                <td className="table_color_column">
                    <div
                        className="table_color_column_square"
                        style={{ backgroundColor: '#' + note.color }}
                    ></div>
                </td>
                <td>
                    {(note.target as TargetModel[])
                        ?.map((x: TargetModel) => (i18n.language === 'fr' ? x.title_fr : x.title_en))
                        .join(', ')}
                </td>
                <td>{i18n.language === 'fr' ? note.type?.title_fr : note.type?.title_en}</td>
                <td>{note.date}</td>
            </tr>
        );
    });
    const component = (
        <>
            <div className="dashboard-container">
                <h1 className="text-center">{t('dashboard_title')}</h1>
                <div className="dashboard-content">
                    <div className="dashboard-content-row">
                        <div className="dashboard-upper-section" id="first-upper-section">
                            <h2 className="text-center dashboard-section-title animate__animated animate__bounceIn">
                                {t('dashboard_group_title')}
                                <span className="dashboard-section-subtitle "> ({groupCount})</span>
                            </h2>
                            <div className="dashboard-groups">{t('dashboard_group_error')}</div>
                            <table className="dashboard-table">
                                <tbody>
                                    <tr>
                                        <th>{t('dashboard_group_header_name')}</th>
                                        <th>{t('dashboard_group_header_year')}</th>
                                        <th>{t('dashboard_group_header_school')}</th>
                                    </tr>
                                    {groupItem}
                                    {groupCount && groupCount >= 0 ? (
                                        <>
                                            <tr key="more_content">
                                                <td colSpan={3} className="text-center">
                                                    ...
                                                </td>
                                            </tr>
                                        </>
                                    ) : null}
                                </tbody>
                            </table>
                            <a className="text-center dashboard-section-cta" href={ROUTE_GROUP}>
                                {t('dashboard_group_cta')}
                            </a>
                        </div>
                        <div className="dashboard-upper-section">
                            <h2 className="text-center dashboard-section-title animate__animated animate__bounceIn">
                                {t('dashboard_student_title')}
                                <span className="dashboard-section-subtitle ">
                                    {' '}
                                    ({studentCount})
                                </span>
                            </h2>
                            <div className="dashboard-students">{t('dasbboard_student_error')}</div>
                            <table className="dashboard-table">
                                <tbody>
                                    <tr>
                                        <th>{t('dashboard_student_header_name')}</th>
                                        <th>{t('dashboard_student_header_year')}</th>
                                        <th>{t('dashboard_student_header_group')}</th>
                                    </tr>
                                    {studentItem}
                                    {studentCount && studentCount >= 0 ? (
                                        <>
                                            <tr key="more_content">
                                                <td colSpan={3} className="text-center">
                                                    ...
                                                </td>
                                            </tr>
                                        </>
                                    ) : null}
                                </tbody>
                            </table>
                            <a className="text-center dashboard-section-cta" href={ROUTE_STUDENT}>
                                {t('dashboard_student_cta')}
                            </a>
                        </div>
                    </div>
                    <div className="dashboard-bottom-section">
                        <h2 className="text-center dashboard-section-title animate__animated animate__bounceIn">
                            {t('dashboard_note_title')}
                            <span className="dashboard-section-subtitle "> ({noteCount})</span>
                        </h2>
                        <div className="dashboard-notes-error">{t('dashboard_note_error')}</div>
                        <table className="dashboard-table">
                            <tbody>
                                <tr>
                                    <th>{t('dashboard_note_header_student')}</th>
                                    <th colSpan={2}>{t('dashboard_note_header_title')}</th>
                                    <th>{t('dashboard_note_header_target')}</th>
                                    <th>{t('dashboard_note_header_type')}</th>
                                    <th>{t('dashboard_note_header_date')}</th>
                                </tr>
                                {noteItem}
                                {noteCount && noteCount >= 0 ? (
                                    <>
                                        <tr key="more_content">
                                            <td colSpan={6} className="text-center">
                                                ...
                                            </td>
                                        </tr>
                                    </>
                                ) : null}
                            </tbody>
                        </table>
                    </div>
                </div>
                <CreateNoteModal ref={ref} />
            </div>
        </>
    );

    return (
        <>
            <Page menu={ROUTE_DASHBOARD} component={component} />
        </>
    );
}
