import i18n from 'i18next';
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Navigate } from 'react-router-dom';

import Page from '../../components/app/Page';
import { IGroupModel } from '../../models/groupModel';
import { IStudentModel } from '../../models/studentModel';
import useNetwork from '../../utils/network/Network';
import * as ROUTES from '../../utils/routes/Routes';
import useToken from '../../utils/useToken';
import CreateStudentModal, { ICreateStudentModalProps } from './modals/CreateStudentModal';

export default function Student() {
    useEffect(() => {
        async function getData() {
            let studentList = await fetchStudents();
            if (studentList.status === 200) {
                const result = await studentList.json().then((json) => JSON.stringify(json));
                setStudents(JSON.parse(result));
            } else {
                (document.querySelector('.dashboard-students') as HTMLDivElement).style.display =
                    'block';
            }
        }
        getData();
    }, [i18n.language]);

    const { token } = useToken();
    const { t } = useTranslation();
    const [students, setStudents] = useState<IStudentModel[] | undefined>(undefined);
    const { fetchStudents } = useNetwork();

    const ref = React.createRef<ICreateStudentModalProps>();

    if (!token) {
        return (
            <>
                <Navigate replace to="/login" />
            </>
        );
    }

    const studentList = students?.map((x: IStudentModel) => (
        <div key={x.id} className="list-link">
            <a className="animate__animated animate__bounceInRight" href={'/student/' + x.id}>
                {x.first_name +
                    ' ' +
                    x.last_name +
                    ' - ' +
                    (x.group as IGroupModel[]).map((x) => x.name).join(', ')}
            </a>
            <hr />
        </div>
    ));

    const component = (
        <div className="list">
            <h1 className="text-center list-header">{t('student_title')}</h1>
            <div className="list-container">
                <div className="list-collapsible">{
                    studentList?.length ?
                        studentList : (
                            <div className="text-center">
                                {t('student_no_results')}
                            </div>
                        )
                }
                </div>
            </div>

            <button onClick={() => ref.current?.openModal()} className="auth-form-btn list-add">
                {t('student_add')}
            </button>
            <CreateStudentModal ref={ref} />
        </div>
    );

    return (
        <>
            <Page menu={ROUTES.ROUTE_STUDENT} component={component} />
        </>
    );
}
