import { EntityRefModel, IEntityRefModel } from './entityRefModel';
import { IStudentModel } from './studentModel';

export interface INoteModel extends IEntityRefModel {
    date: string;
    title: string;
    description: string;
    color: string;
    type: INoteTypeModel;
    target: ITargetModel[] | IEntityRefModel[];
    student: IEntityRefModel | IStudentModel;
}

export class NoteModel extends EntityRefModel implements INoteModel {
    date: string;
    title: string;
    description: string;
    color: string;
    type: INoteTypeModel;
    target: ITargetModel[] | IEntityRefModel[];
    student: IEntityRefModel | IStudentModel;

    constructor(props: INoteModel) {
        super(props);
        this.date = props.date;
        this.title = props.title;
        this.description = props.description;
        this.color = props.color;
        this.type = props.type;
        this.target = props.target;
        this.student = props.student;
    }

    toJson() {
        console.log(this)
        const obj = {
            ...this,
            id: this.id === -1 ? null : this.id,
            student: this.student.id,
            target: this.target.map((element: IEntityRefModel) => element.id),
            type: this.type.id,
        };
        return JSON.stringify(obj);
    }
}

export interface INoteTypeModel extends IEntityRefModel {
    title_fr: string;
    title_en: string;
}

export class NoteTypeModel extends EntityRefModel implements INoteTypeModel {
    title_fr: string;
    title_en: string;

    constructor(props: INoteTypeModel) {
        super(props);
        this.title_fr = props.title_fr;
        this.title_en = props.title_en;
    }

    toJson() {
        return JSON.stringify(this);
    }
}

export interface ITargetModel extends IEntityRefModel {
    title_fr: string;
    title_en: string;
}

export class TargetModel extends EntityRefModel implements ITargetModel {
    title_fr: string;
    title_en: string;

    constructor(props: INoteTypeModel) {
        super(props);
        this.title_fr = props.title_fr;
        this.title_en = props.title_en;
    }

    toJson() {
        return JSON.stringify(this);
    }
}
