import { library } from '@fortawesome/fontawesome-svg-core';
import {
    faCalendar,
    faClose,
    faComment,
    faFont,
    faFontAwesome,
} from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import i18n from 'i18next';
import Moment from 'moment';
import React, {
    ForwardedRef,
    forwardRef,
    useEffect,
    useImperativeHandle,
    useRef,
    useState,
} from 'react';
import { useTranslation } from 'react-i18next';
import Modal from 'react-modal';
import { createEditor } from 'slate';
import { Descendant } from 'slate';
import { withHistory } from 'slate-history';
import { withReact } from 'slate-react';

import EditorWysiwyg, { withImages } from '../../../components/editor/EditorWysiwyg';
import ErrorBoundary from '../../../components/errors/ErrorBoundary';
import { IEntityRefModel } from '../../../models/entityRefModel';
import { INoteModel, INoteTypeModel, ITargetModel, NoteModel, TargetModel } from "../../../models/noteModel";
import useNetwork from '../../../utils/network/Network';

export interface ICreateNoteModalProps {
    openModal(note?: INoteModel, studentId?: IEntityRefModel): void;
}

function CreateNoteModal(props: any, ref: ForwardedRef<ICreateNoteModalProps>) {
    const [modalIsOpen, setIsOpen] = React.useState(false);
    const { t } = useTranslation();

    const [noteTypes, setNoteTypes] = useState<INoteTypeModel[] | undefined>(undefined);
    const [availableTargets, setAvailableTargets] = useState<ITargetModel[] | undefined>(undefined);

    const [noteId, setNoteId] = useState<IEntityRefModel | undefined>();
    const [studentId, setStudentId] = useState<IEntityRefModel | undefined>();

    const [title, setTitle] = useState<string>();
    const [description, setDescription] = useState<string>();
    const [date, setDate] = useState<string>();
    const [type, setType] = useState<INoteTypeModel | undefined>();
    const [color, setColor] = useState<string>();
    const [targets, setTargets] = useState<ITargetModel | undefined>();
    const { fetchNoteTypes, fetchTargets, createNote, updateNote, deleteNote } = useNetwork();

    const editorRef = useRef();
    if (!editorRef.current) editorRef.current = withImages(withHistory(withReact(createEditor())));
    const editor = editorRef.current;

    const initialValue: Descendant[] = [
        {
            type: 'paragraph',
            children: [{ text: ' ' }],
        },
    ];

    let now = new Date();

    const [editorValue, setEditorValue] = useState<Descendant[]>(initialValue);

    library.add(faComment, faFont, faClose, faCalendar, faFontAwesome);

    useImperativeHandle(ref, () => {
        return {
            openModal(note?: INoteModel, studentId?: IEntityRefModel) {
                setStudentId(studentId);
                if (note) {
                    setNoteId(note!);
                    setType(note!.type);
                    setTitle(note!.title);
                    setDescription(note!.description);
                    setTargets(note!.target?.at(0) as TargetModel);
                    setDate(note!.date);
                    setColor(note!.color);

                    try {
                        setEditorValue(JSON.parse(note!.description) as Descendant[]);
                    } catch (e: any) {
                        setEditorValue(initialValue);
                    }
                } else {
                    setNoteId(undefined);
                    setType(noteTypes?.at(0));
                    setTitle('');
                    setDescription('');
                    setTargets(availableTargets?.at(0));
                    setDate(Moment(now).format('YYYY-MM-DD'));
                    setColor('');

                    setEditorValue(initialValue);
                }
                setIsOpen(true);
            },
        };
    });

    useEffect(() => {
        async function getData() {
            let noteTypesList = await fetchNoteTypes();
            if (noteTypesList.status == 200) {
                const result = await noteTypesList.json().then((json) => JSON.stringify(json));
                setNoteTypes(JSON.parse(result));
            }

            let availableTargetsList = await fetchTargets();
            if (availableTargetsList.status == 200) {
                const result = await availableTargetsList
                    .json()
                    .then((json) => JSON.stringify(json));
                setAvailableTargets(JSON.parse(result));
            }
        }
        getData();
    }, [i18n.language]);

    function closeModal() {
        window.location.reload();
    }

    const customStyles = {
        content: {
            top: '1%',
            left: '1%',
            right: '1%',
            bottom: '1%',
        },
    };
    Modal.setAppElement('body');

    let noteTypesList = noteTypes?.map((x: INoteTypeModel) => {
        return (
            <option key={x.id} value={x.id}>
                {i18n.language === 'fr' ? x.title_fr : x.title_en}
            </option>
        );
    });

    noteTypesList?.unshift(
        <option disabled key={-1} value="">
            {t('student_details_note_header_type')}
        </option>
    );

    let availableTargetList = availableTargets?.map((x: ITargetModel) => {
        return (
            <option key={x.id} value={x.id}>
                {i18n.language === 'fr' ? x.title_fr : x.title_en}
            </option>
        );
    });
    availableTargetList?.unshift(
        <option disabled key={-1} value="">
            {t('student_details_note_header_target')}
        </option>
    );

    const handleDelete = async (e: any) => {
        e.preventDefault();
        if (window.confirm(t('note_modal_delete_confirmation'))) {
            let noteDeletion = await deleteNote(noteId!);
            if (noteDeletion.status == 204) {
                window.location.reload();
            }
        }
    };

    const handleSubmit = async (e: any) => {
        e.preventDefault();
        const note = new NoteModel({
            id: noteId?.id ?? -1,
            title: title ?? '',
            description: description ?? '',
            color: color ?? '',
            type: type!,
            target: [targets!],
            date: Moment(date).format('YYYY-MM-DD'),
            student: studentId!,
        });

        if (noteId) {
            let noteUpdateResult = await updateNote(note);
            if (noteUpdateResult.status == 200) {
                window.location.reload();
            }
        } else {
            let noteCreationResult = await createNote(note);
            if (noteCreationResult.status == 201) {
                window.location.reload();
            }
        }
    };
    return (
        <Modal
            isOpen={modalIsOpen}
            onRequestClose={closeModal}
            contentLabel={t('signup_modal_title')}
            style={customStyles}
        >
            <div className="create-note-modal-container signup-modal animate__animated animate__zoomIn">
                <div>
                    <h2 className="list-creation-title ">
                        {noteId ? t('note_modal_title_update') : t('note_modal_title_create')}
                    </h2>
                    <FontAwesomeIcon
                        className="note-creation-form-exit"
                        icon="close"
                        onClick={closeModal}
                    />

                    <form onSubmit={handleSubmit}>
                        <div className="student-creation-form-header-container">
                            <div className="wrap-input validate-input create-note-modal-title-field">
                                <input
                                    required
                                    className="input"
                                    type="text"
                                    defaultValue={title}
                                    onChange={(e) => setTitle(e.target.value)}
                                    placeholder="Titre"
                                />
                                <span className="focus-input"></span>
                                <span className="symbol-input">
                                    <FontAwesomeIcon icon="font" />
                                </span>
                            </div>
                            <div className="wrap-input validate-input create-note-modal-xxsmall-field">
                                <select
                                    id="color-selector"
                                    required
                                    className="input input--small"
                                    defaultValue={color}
                                    style={
                                        color
                                            ? { color: '#' + color, borderColor: '#' + color }
                                            : {}
                                    }
                                    onChange={(e) => {
                                        setColor(e.target.value);
                                        document.getElementById('color-selector')!.style.color =
                                            '#' + e.target.value;
                                        document.getElementById(
                                            'color-selector'
                                        )!.style.borderColor = '#' + e.target.value;
                                        document.getElementById('color_icon')!.style.color =
                                            '#' + e.target.value;
                                    }}
                                >
                                    <option disabled key={-1} value="">
                                        {t('student_details_note_header_color')}
                                    </option>
                                    <option key={0} value="ff9999">
                                        {t('color_name_red')}
                                    </option>
                                    <option key={1} value="189ab4">
                                        {t('color_name_blue')}
                                    </option>
                                    <option key={2} value="05445e">
                                        {t('color_name_dark')}
                                    </option>
                                    <option key={3} value="7fbf7f">
                                        {t('color_name_green')}
                                    </option>
                                </select>
                                <span className="focus-input"></span>
                                <span className="symbol-input">
                                    <FontAwesomeIcon
                                        style={color ? { color: '#' + color } : {}}
                                        id="color_icon"
                                        icon="font-awesome"
                                    />
                                </span>
                            </div>
                            <div className="student-creation-form-header-container--right">
                                <div className="wrap-input validate-input create-note-modal-small-field">
                                    <select
                                        required
                                        onChange={(e) => {
                                            setTargets(
                                                availableTargets?.filter((target: ITargetModel) => {
                                                    return target.id === +e.target.value;
                                                })[0]
                                            );
                                        }}
                                        defaultValue={targets?.id}
                                        className="input input--small"
                                    >
                                        {availableTargetList}
                                    </select>
                                    <span className="focus-input"></span>
                                    <span className="symbol-input">
                                        <FontAwesomeIcon icon="comment" />
                                    </span>
                                </div>
                                <div className="wrap-input validate-input create-note-modal-small-field">
                                    <select
                                        required
                                        onChange={(e) =>
                                            setType(
                                                noteTypes?.filter((noteType: INoteTypeModel) => {
                                                    return noteType.id === +e.target.value;
                                                })[0]
                                            )
                                        }
                                        defaultValue={type?.id}
                                        className="input input--small"
                                    >
                                        {noteTypesList}
                                    </select>
                                    <span className="focus-input"></span>
                                    <span className="symbol-input">
                                        <FontAwesomeIcon icon="comment" />
                                    </span>
                                </div>
                            </div>
                        </div>
                        <ErrorBoundary error={t('note_editor_error')}>
                            <EditorWysiwyg
                                editor={editor}
                                initialValue={editorValue}
                                onChange={(value) => {
                                    setDescription(JSON.stringify(value));
                                }}
                            />
                        </ErrorBoundary>

                        <br />
                        <div className="create-note-footer-container">
                            <button
                                onClick={handleDelete}
                                className="auth-form-btn create-note-modal-xsmall-field create-note-delete"
                            >
                                {t('note_modal_delete')}
                            </button>
                            <div className="create-note-inner-container">
                                <div className="wrap-input validate-input create-note-modal-xsmall-field">
                                    <input
                                        type="date"
                                        required
                                        defaultValue={date!}
                                        onChange={(e) => setDate(e.target.value)}
                                        className="input-date"
                                    ></input>
                                </div>
                                <button className="auth-form-btn create-note-modal-xsmall-field">
                                    {noteId ? t('note_modal_update') : t('note_modal_submit')}
                                </button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </Modal>
    );
}

export default forwardRef(CreateNoteModal);
