import { EntityRefModel, IEntityRefModel } from './entityRefModel';
import { IGroupModel } from './groupModel';

export interface IStudentModel extends IEntityRefModel {
    first_name: string;
    last_name: string;
    year: number;
    description?: string;
    group?: IGroupModel[];
}

export class StudentModel extends EntityRefModel implements IStudentModel {
    first_name: string;
    last_name: string;
    year: number;
    description: string;
    group: IGroupModel[];

    constructor(props: IStudentModel) {
        super(props);
        this.first_name = props.first_name;
        this.last_name = props.last_name;
        this.year = props.year;
        this.description = props.description ?? '';
        this.group = props.group ?? [];
    }

    toJson() {
        const obj = {
            ...this,
            group: this.group.map((element: IEntityRefModel) => element.id),
        };
        return JSON.stringify(obj);
    }
}
