import 'react-minimal-side-navigation/lib/ReactMinimalSideNavigation.css';

import { library } from '@fortawesome/fontawesome-svg-core';
import {
    faCalendar,
    faGauge,
    faGraduationCap,
    faLanguage,
    faPeopleGroup,
    faPersonRunning,
} from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import i18n from 'i18next';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { Navigation } from 'react-minimal-side-navigation';
import { useNavigate } from 'react-router-dom';

import { ReactComponent as LogoSvg } from '../../logo.svg';
import * as ROUTES from '../../utils/routes/Routes';
import useToken from '../../utils/useToken';

const LOG_OFF_ITEM_ID = 'logoff';
const LANG_ITEM_ID = 'lang';

export default function Sidebar(props: ISideBarProps) {
    const navigate = useNavigate();
    const { resetToken } = useToken();
    const { t } = useTranslation();

    library.add(faGauge, faCalendar, faPersonRunning, faGraduationCap, faPeopleGroup, faLanguage);

    return (
        <div className="sidebar">
            <Navigation
                activeItemId={props.menu}
                onSelect={({ itemId }) => {
                    navigate(itemId);
                }}
                items={[
                    {
                        itemId: ROUTES.ROUTE_ROOT,
                        title: '',
                        elemBefore: () => (
                            <div className="sidebar-logo">
                                <LogoSvg height="150px" />
                            </div>
                        ),
                    },
                    {
                        title: t('sidebar_dashboard'),
                        itemId: ROUTES.ROUTE_DASHBOARD,
                        elemBefore: () => <FontAwesomeIcon icon="gauge" />,
                    },
                    {
                        title: t('sidebar_student'),
                        itemId: ROUTES.ROUTE_STUDENT,
                        elemBefore: () => <FontAwesomeIcon icon="graduation-cap" />,
                    },
                    {
                        title: t('sidebar_group'),
                        itemId: ROUTES.ROUTE_GROUP,
                        elemBefore: () => <FontAwesomeIcon icon="people-group" />,
                    },
                    {
                        title: t('sidebar_calendar'),
                        itemId: ROUTES.ROUTE_CALENDAR,
                        elemBefore: () => <FontAwesomeIcon icon="calendar" />,
                    },
                ]}
            />
            <Navigation
                onSelect={({ itemId }) => {
                    if (itemId === LANG_ITEM_ID) {
                        i18n.changeLanguage(i18n.language === 'fr' ? 'en' : 'fr');
                    } else if (itemId === LOG_OFF_ITEM_ID) {
                        resetToken();
                        navigate(ROUTES.ROUTE_LOGIN);
                    }
                }}
                items={[
                    {
                        itemId: LANG_ITEM_ID,
                        title: t('sidebar_lang'),
                        elemBefore: () => <FontAwesomeIcon icon="language" />,
                    },
                    {
                        itemId: LOG_OFF_ITEM_ID,
                        title: t('sidebar_disconnect'),
                        elemBefore: () => <FontAwesomeIcon icon="person-running" />,
                    },
                ]}
                activeItemId={Math.random().toString()}
            />
        </div>
    );
}

export interface ISideBarProps {
    menu: string;
}
