import React from 'react';
import { BrowserRouter, Navigate, Route, Routes } from 'react-router-dom';

import Calendar from '../../pages/calendar/Calendar';
import Dashboard from '../../pages/dashboard/Dashboard';
import Group from '../../pages/group/Group';
import GroupDetails from '../../pages/group/GroupDetails';
import Login from '../../pages/login/Login';
import NotFound from '../../pages/notFound/NotFound';
import SignUp from '../../pages/signUp/SignUp';
import Student from '../../pages/student/Student';
import StudentDetails from '../../pages/student/StudentDetails';
import * as ROUTES from '../../utils/routes/Routes';
import useToken from '../../utils/useToken';

export default function Router() {
    const { token } = useToken();
    return (
        <BrowserRouter>
            <Routes>
                <Route path={ROUTES.ROUTE_LOGIN} element={<Login />} />
                <Route path={ROUTES.ROUTE_DASHBOARD} element={<Dashboard />} />
                <Route path={ROUTES.ROUTE_STUDENT} element={<Student />} />
                <Route path={ROUTES.ROUTE_GROUP} element={<Group />} />
                <Route path={ROUTES.ROUTE_STUDENT_DETAIL + ':id'} element={<StudentDetails />} />
                <Route path={ROUTES.ROUTE_GROUP_DETAIL + ':id'} element={<GroupDetails />} />
                <Route path={ROUTES.ROUTE_SIGNUP} element={<SignUp />} />
                <Route path={ROUTES.ROUTE_CALENDAR} element={<Calendar />} />

                <Route
                    path={ROUTES.ROUTE_ROOT}
                    element={
                        token ? (
                            <Navigate replace to="/dashboard" />
                        ) : (
                            <Navigate replace to="/login" />
                        )
                    }
                />
                <Route path="*" element={<NotFound />} />
            </Routes>
        </BrowserRouter>
    );
}
