import { library } from '@fortawesome/fontawesome-svg-core';
import { faCalendar, faUser } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { ForwardedRef, forwardRef, useImperativeHandle, useState } from 'react';
import { useTranslation } from 'react-i18next';
import Modal from 'react-modal';
import { useNavigate } from 'react-router-dom';

import { StudentModel } from '../../../models/studentModel';
import useNetwork from '../../../utils/network/Network';
import * as ROUTES from '../../../utils/routes/Routes';

export interface ICreateStudentModalProps {
    openModal(): void;
}

function CreateStudentModal(props: any, ref: ForwardedRef<ICreateStudentModalProps>) {
    const { t } = useTranslation();
    const [modalIsOpen, setIsOpen] = React.useState(false);

    function closeModal() {
        setIsOpen(false);
    }

    useImperativeHandle(ref, () => {
        return {
            openModal() {
                setIsOpen(true);
            },
        };
    });

    const customStyles = {
        content: {
            top: '50%',
            left: '50%',
            right: 'auto',
            bottom: 'auto',
            marginRight: '-50%',
            transform: 'translate(-50%, -50%)',
        },
    };
    library.add(faUser, faCalendar);
    Modal.setAppElement('body');

    const [newStudentFirstName, setNewStudentFirstName] = useState<string>();
    const [newStudentLastName, setNewStudentLastName] = useState<string>();
    const [newStudentYear, setNewStudentYear] = useState<string>();
    const { createStudent } = useNetwork();
    const navigate = useNavigate();

    const handleSubmit = async (e: any) => {
        e.preventDefault();

        const user = await createStudent(
            new StudentModel({
                first_name: newStudentFirstName!,
                last_name: newStudentLastName!,
                year: +newStudentYear!,
            })
        );
        if (user.status === 201) {
            const info = await user.json().then((json) => JSON.stringify(json));
            const student = JSON.parse(info);
            navigate(ROUTES.ROUTE_STUDENT_DETAIL + student.id);
        }
    };

    return (
        <Modal
            isOpen={modalIsOpen}
            onRequestClose={closeModal}
            contentLabel={t('signup_modal_title')}
            style={customStyles}
        >
            <div className="signup-modal animate__animated animate__jackInTheBox">
                <h2 className="list-creation-title">{t('student_creation_title')}</h2>
                <form className="auth-form validate-form" onSubmit={handleSubmit}>
                    <div className="wrap-input validate-input">
                        <input
                            required
                            className="input"
                            type="text"
                            onChange={(e) => setNewStudentFirstName(e.target.value)}
                            placeholder={t('student_field_first_name')}
                        />
                        <span className="focus-input"></span>
                        <span className="symbol-input">
                            <FontAwesomeIcon icon="user" />
                        </span>
                    </div>
                    <div className="wrap-input validate-input">
                        <input
                            required
                            className="input"
                            type="text"
                            onChange={(e) => setNewStudentLastName(e.target.value)}
                            placeholder={t('student_field_last_name')}
                        />
                        <span className="focus-input"></span>
                        <span className="symbol-input">
                            <FontAwesomeIcon icon="user" />
                        </span>
                    </div>
                    <div className="wrap-input validate-input">
                        <select
                            required
                            defaultValue=""
                            onChange={(e) => setNewStudentYear(e.target.value)}
                            className="input"
                        >
                            <option value="" disabled hidden>
                                {t('student_field_year')}
                            </option>
                            <option value="0">{t('year_0')}</option>
                            <option value="1">{t('year_1')}</option>
                            <option value="2">{t('year_2')}</option>
                            <option value="3">{t('year_3')}</option>
                            <option value="4">{t('year_4')}</option>
                            <option value="5">{t('year_5')}</option>
                            <option value="6">{t('year_6')}</option>
                        </select>
                        <span className="focus-input"></span>
                        <span className="symbol-input">
                            <FontAwesomeIcon icon="calendar" />
                        </span>
                    </div>
                    <br />
                    <button className="auth-form-btn">{t('student_submit')}</button>
                </form>
            </div>
        </Modal>
    );
}

export default forwardRef(CreateStudentModal);
