/* eslint-disable */
import React, { useCallback } from 'react';
import isHotkey from 'is-hotkey';
import {
    Editable,
    useSlate,
    Slate,
    useSlateStatic,
    ReactEditor,
    useSelected,
    useFocused,
} from 'slate-react';
import { Editor, Transforms, Descendant, Element as SlateElement } from 'slate';
import isUrl from 'is-url';

import { Button, Icon, Toolbar } from './EditorWysiwygComponents';
import { ImageElement } from './EditorElement.d';
import { css } from '@emotion/css';

const HOTKEYS = {
    'mod+b': 'bold',
    'mod+i': 'italic',
    'mod+u': 'underline',
    'mod+`': 'code',
};

const LIST_TYPES = ['numbered-list', 'bulleted-list'];
const TEXT_ALIGN_TYPES = ['left', 'center', 'right', 'justify'];
const FONT_SIZE = 'font-family';

// @ts-ignore
export const withImages = (editor) => {
    const { insertData, isVoid } = editor;
    // @ts-ignore
    editor.isVoid = (element) => {
        return element.type === 'image' ? true : isVoid(element);
    };
    // @ts-ignore
    editor.insertData = (data) => {
        const text = data.getData('text/plain');
        const { files } = data;

        if (files && files.length > 0) {
            for (const file of files) {
                const reader = new FileReader();
                const [mime] = file.type.split('/');

                if (mime === 'image') {
                    reader.addEventListener('load', () => {
                        const url = reader.result;
                        insertImage(editor, url);
                    });

                    reader.readAsDataURL(file);
                }
            }
        } else if (isImageUrl(text)) {
            insertImage(editor, text);
        } else {
            insertData(data);
        }
    };

    return editor;
};
// @ts-ignore
const insertImage = (editor, url) => {
    const text = { text: '' };
    const image: ImageElement = { type: 'image', url, children: [text] };
    Transforms.insertNodes(editor, image);
};

// @ts-ignore
const Image = ({ attributes, children, element }) => {
    const editor = useSlateStatic();
    const path = ReactEditor.findPath(editor, element);

    const selected = useSelected();
    const focused = useFocused();
    return (
        <div {...attributes}>
            {children}
            <div
                contentEditable={false}
                className={css`
                    position: relative;
                `}
            >
                <img
                    src={element.url}
                    className={css`
                        display: block;
                        max-width: 100%;
                        max-height: 20em;
                        box-shadow: ${selected && focused ? '0 0 0 3px #B4D5FF' : 'none'};
                    `}
                />
                <Button
                    active
                    onClick={() => Transforms.removeNodes(editor, { at: path })}
                    className={css`
                        display: ${selected && focused ? 'inline' : 'none'};
                        position: absolute;
                        top: 0.5em;
                        left: 0.5em;
                        background-color: white;
                    `}
                >
                    <Icon>delete</Icon>
                </Button>
            </div>
        </div>
    );
};

const InsertImageButton = () => {
    const editor = useSlateStatic();
    return (
        <Button
            // @ts-ignore
            onMouseDown={(event) => {
                event.preventDefault();
                const url = window.prompt('Enter the URL of the image:');
                if (url && !isImageUrl(url)) {
                    alert('URL is not an image');
                    return;
                }
                insertImage(editor, url);
            }}
        >
            <Icon>image</Icon>
        </Button>
    );
};
// @ts-ignore
const isImageUrl = (url) => {
    if (!url) return false;
    if (!isUrl(url)) return false;
    return true;
};

function EditorWysiwyg(props: {
    editor: any;
    initialValue: Descendant[];
    onChange: ((value: Descendant[]) => void) | undefined;
}) {
    // @ts-ignore
    const renderElement = useCallback((props) => <Element {...props} />, []);
    // @ts-ignore
    const renderLeaf = useCallback((props) => <Leaf {...props} />, []);

    return (
        <div className="editor">
            <Slate editor={props.editor} value={props.initialValue} onChange={props.onChange}>
                <Toolbar>
                    <MarkButton format="bold" icon="format_bold" />
                    <MarkButton format="italic" icon="format_italic" />
                    <MarkButton format="underline" icon="format_underlined" />
                    <MarkButton format="code" icon="code" />
                    <FontFamilyButton />
                    <BlockButton format="heading-one" icon="looks_one" />
                    <BlockButton format="heading-two" icon="looks_two" />
                    <BlockButton format="block-quote" icon="format_quote" />
                    <BlockButton format="numbered-list" icon="format_list_numbered" />
                    <BlockButton format="bulleted-list" icon="format_list_bulleted" />
                    <BlockButton format="left" icon="format_align_left" />
                    <BlockButton format="center" icon="format_align_center" />
                    <BlockButton format="right" icon="format_align_right" />
                    <BlockButton format="justify" icon="format_align_justify" />
                    <InsertImageButton />
                </Toolbar>
                <Editable
                    renderElement={renderElement}
                    renderLeaf={renderLeaf}
                    spellCheck
                    autoFocus
                    onKeyDown={(event) => {
                        for (const hotkey in HOTKEYS) {
                            if (isHotkey(hotkey, event as any)) {
                                event.preventDefault();
                                // @ts-ignore
                                const mark = HOTKEYS[hotkey];
                                toggleMark(props.editor, mark);
                            }
                        }
                    }}
                />
            </Slate>
        </div>
    );
}

// @ts-ignore
const toggleBlock = (editor, format) => {
    const isActive = isBlockActive(
        editor,
        format,
        TEXT_ALIGN_TYPES.includes(format) ? 'align' : 'type'
    );
    const isList = LIST_TYPES.includes(format);

    Transforms.unwrapNodes(editor, {
        match: (n) =>
            !Editor.isEditor(n) &&
            SlateElement.isElement(n) &&
            LIST_TYPES.includes(n.type) &&
            !TEXT_ALIGN_TYPES.includes(format),
        split: true,
    });
    let newProperties: Partial<SlateElement>;
    if (TEXT_ALIGN_TYPES.includes(format)) {
        newProperties = {
            // @ts-ignore
            align: isActive ? undefined : format,
        };
    } else {
        newProperties = {
            type: isActive ? 'paragraph' : isList ? 'list-item' : format,
        };
    }
    Transforms.setNodes<SlateElement>(editor, newProperties);

    if (!isActive && isList) {
        const block = { type: format, children: [] };
        Transforms.wrapNodes(editor, block);
    }
};
// @ts-ignore
const toggleMark = (editor, format) => {
    const isActive = isMarkActive(editor, format);

    if (isActive) {
        Editor.removeMark(editor, format);
    } else {
        Editor.addMark(editor, format, true);
    }
};
// @ts-ignore
const isBlockActive = (editor, format, blockType = 'type') => {
    const { selection } = editor;
    if (!selection) return false;

    const [match] = Array.from(
        Editor.nodes(editor, {
            at: Editor.unhangRange(editor, selection),
            match: (n) =>
                !Editor.isEditor(n) &&
                SlateElement.isElement(n) &&
                // @ts-ignore
                n[blockType] === format,
        })
    );

    return !!match;
};

// @ts-ignore
const isMarkActive = (editor, format) => {
    try {
        const marks = Editor.marks(editor);
        // @ts-ignore
        return marks ? marks[format] === true : false;
    } catch (e: any) {
        return false;
    }
};

// @ts-ignore
const Element = (props: any) => {
    const { attributes, children, element } = props;
    const style = { fontFamily: element.fontName, textAlign: element.align };

    switch (element.type) {
        case 'image':
            return <Image style={style} {...props} />;
        case 'block-quote':
            return (
                <blockquote style={style} {...attributes}>
                    {children}
                </blockquote>
            );
        case 'bulleted-list':
            return (
                <ul style={style} {...attributes}>
                    {children}
                </ul>
            );
        case 'heading-one':
            return (
                <h1 style={style} {...attributes}>
                    {children}
                </h1>
            );
        case 'heading-two':
            return (
                <h2 style={style} {...attributes}>
                    {children}
                </h2>
            );
        case 'list-item':
            return (
                <li style={style} {...attributes}>
                    {children}
                </li>
            );
        case 'numbered-list':
            return (
                <ol style={style} {...attributes}>
                    {children}
                </ol>
            );
        case 'font-family':
            return (
                <div style={style} {...attributes}>
                    {children}
                </div>
            );
        default:
            return (
                <p style={style} {...attributes}>
                    {children}
                </p>
            );
    }
};
// @ts-ignore
const Leaf = ({ attributes, children, leaf }) => {
    if (leaf.bold) {
        children = <strong>{children}</strong>;
    }

    if (leaf.code) {
        children = <code>{children}</code>;
    }

    if (leaf.italic) {
        children = <em>{children}</em>;
    }

    if (leaf.underline) {
        children = <u>{children}</u>;
    }

    return <span {...attributes}>{children}</span>;
};
// @ts-ignore
const BlockButton = ({ format, icon }) => {
    const editor = useSlate();
    return (
        <Button
            active={isBlockActive(
                editor,
                format,
                TEXT_ALIGN_TYPES.includes(format) ? 'align' : 'type'
            )}
            // @ts-ignore
            onMouseDown={(event) => {
                event.preventDefault();
                toggleBlock(editor, format);
            }}
        >
            <Icon>{icon}</Icon>
        </Button>
    );
};

// @ts-ignore
const activeFontName = (editor) => {
    const { selection } = editor;
    if (!selection) return 'Poppins';

    // @ts-ignore
    try {
        return editor.children.at(selection.focus.path[0]).fontName ?? 'Poppins';
    } catch (e: any) {
        return 'Poppins';
    }
};

// @ts-ignore
const fontUpdate = (editor) => {
    const element = document.getElementById('font-selector') as HTMLSelectElement;
    if (element) {
        element.value = activeFontName(editor);
    }
};

const FontFamilyButton = () => {
    const editor = useSlate();

    return (
        <Button active={fontUpdate(editor)}>
            <select
                id="font-selector"
                defaultValue={activeFontName(editor)}
                onChange={(e: any) => {
                    e.preventDefault();
                    Transforms.setNodes(
                        editor,
                        // @ts-ignore
                        { type: 'font-family', fontName: e.target.value },
                        { match: (n) => Editor.isBlock(editor, n) }
                    );
                }}
            >
                <optgroup label="Sans serif">
                    <option value="Arial">Arial</option>
                    <option value="Helvetica">Helvetica</option>
                    <option value="Poppins">Poppins</option>
                </optgroup>
                <optgroup label="Serif">
                    <option value="Times New Roman">Times New Roman</option>
                    <option value="Times-Roman">Times-Roman</option>
                    <option value="Montserrat">Montserrat</option>
                </optgroup>
                <optgroup label="Mono">
                    <option value="Courier New">Courier New</option>
                    <option value="Courier">Courier</option>
                </optgroup>
            </select>
        </Button>
    );
};

// @ts-ignore
const MarkButton = ({ format, icon }) => {
    const editor = useSlate();
    return (
        <Button
            active={isMarkActive(editor, format)}
            // @ts-ignore
            onMouseDown={(event) => {
                event.preventDefault();
                toggleMark(editor, format);
            }}
        >
            <Icon>{icon}</Icon>
        </Button>
    );
};

export default EditorWysiwyg;
