import { library } from '@fortawesome/fontawesome-svg-core';
import {
    faEnvelope,
    faFileSignature,
    faLock,
    faLongArrowRight,
    faSignature,
    faUserGraduate,
} from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import i18n from "i18next";
import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';
import Modal from 'react-modal';
import { useNavigate } from 'react-router-dom';
import { Navigate } from 'react-router-dom';

import network from '../../utils/network/Network';
import * as ROUTES from '../../utils/routes/Routes';
import useToken from '../../utils/useToken';

export default function SignUp() {
    const [username, setUserName] = useState<string>();
    const [password, setPassword] = useState<string>();
    const [firstName, setFirstName] = useState<string>();
    const [lastName, setLastName] = useState<string>();
    const [email, setEmail] = useState<string>();
    const [modalIsOpen, setIsOpen] = React.useState(false);
    const { createUser } = network();
    const navigate = useNavigate();
    const { token } = useToken();
    const { t } = useTranslation();

    function openModal() {
        setIsOpen(true);
    }

    function closeModal() {
        navigate(ROUTES.ROUTE_LOGIN);
    }

    if (token) {
        return (
            <>
                <Navigate replace to={ROUTES.ROUTE_DASHBOARD} />
            </>
        );
    }

    Modal.setAppElement('body');

    const handleSubmit = async (e: any) => {
        e.preventDefault();

        (document.getElementById('auth-error') as HTMLDivElement).style.display = 'none';
        const passwordReg = /\d/;

        if (password && (password.length < 8 || !passwordReg.test(password))) {
            (document.getElementById('auth-error') as HTMLDivElement).style.display = 'block';
        } else {
            //Create user to backend
            const loginInfos = await createUser({
                username,
                email,
                firstName,
                lastName,
                password,
            });

            if (loginInfos.status !== 201) {
                const errorStr = await loginInfos.json().then((json) => JSON.stringify(json));
                const error = JSON.parse(errorStr);
                let errorElement = document.getElementById('auth-error');
                if (error.username) {
                    errorElement!.innerHTML = t('signup_error_username');
                }
                if (error.email) {
                    errorElement!.innerHTML = t('signup_error_email');
                }
                errorElement!.style.display = 'block';
            } else {
                //Display modal to lead the user to login route
                openModal();
            }
        }
    };

    const customStyles = {
        content: {
            top: '50%',
            left: '50%',
            right: 'auto',
            bottom: 'auto',
            marginRight: '-50%',
            transform: 'translate(-50%, -50%)',
        },
    };
    let modal = (
        <Modal
            isOpen={modalIsOpen}
            onRequestClose={closeModal}
            contentLabel={t('signup_modal_title')}
            style={customStyles}
        >
            <div className="signup-modal">
                <h2>{t('signup_modal_title')}</h2>
                <p>{t('signup_modal_description')}</p>
                <br />
                <button className="auth-form-btn" onClick={closeModal}>
                    {t('signup_modal_confirm')}
                </button>
            </div>
        </Modal>
    );
    library.add(faEnvelope, faFileSignature, faSignature, faLock, faLongArrowRight, faUserGraduate);
    return (
        <div className="container-auth">
            <div className="wrap-auth wrap-auth--column">
                <span className="auth-form-title ">{t('signup_title')}</span>
                <form className="signUp-form auth-form validate-form" onSubmit={handleSubmit}>
                    <div>
                        <div className="wrap-input validate-input">
                            <input
                                className="input"
                                type="text"
                                maxLength={20}
                                onChange={(e: any) => setUserName(e.target.value)}
                                placeholder={t('signup_username_label')}
                            />
                            <span className="focus-input"></span>
                            <span className="symbol-input">
                                <FontAwesomeIcon icon="user-graduate" />
                            </span>
                        </div>
                        <div className="wrap-input validate-input">
                            <input
                                className="input"
                                type="email"
                                onChange={(e) => setEmail(e.target.value)}
                                placeholder={t('signup_email_label')}
                            />
                            <span className="focus-input"></span>
                            <span className="symbol-input">
                                <FontAwesomeIcon icon="envelope" />
                            </span>
                        </div>
                        <div className="wrap-input validate-input signup-nomargin">
                            <input
                                className="input"
                                type="password"
                                onChange={(e) => setPassword(e.target.value)}
                                placeholder={t('signup_password_label')}
                            />
                            <span className="focus-input"></span>
                            <span className="symbol-input">
                                <FontAwesomeIcon icon="lock" />
                            </span>
                        </div>
                        <div className="text-center regular-text " id="auth-error">
                            {t('signup_password_error')}
                        </div>
                        <div className="text-center">
                            <span className="light-text small-text">
                                {t('signup_password_instruction')}
                            </span>
                        </div>
                        <div className="text-center regular-text " id="auth-error">
                            {t('signup_connection_error')}
                        </div>
                    </div>
                    <div>
                        <div className="wrap-input validate-input">
                            <input
                                className="input"
                                type="text"
                                onChange={(e) => setFirstName(e.target.value)}
                                placeholder={t('signup_first_name_label')}
                            />
                            <span className="focus-input"></span>
                            <span className="symbol-input">
                                <FontAwesomeIcon icon="signature" />
                            </span>
                        </div>
                        <div className="wrap-input validate-input">
                            <input
                                className="input"
                                type="text"
                                onChange={(e) => setLastName(e.target.value)}
                                placeholder={t('signup_last_name_label')}
                            />
                            <span className="focus-input"></span>
                            <span className="symbol-input">
                                <FontAwesomeIcon icon="file-signature" />
                            </span>
                        </div>
                        <div className="container-signup-form-btn">
                            <button className="auth-form-btn">
                                {t('signup_create')}&nbsp;
                                <FontAwesomeIcon icon="long-arrow-right" />
                            </button>
                        </div>
                    </div>
                </form>
                <div className="text-center signup-connect">
                    <a className="regular-text" href={ROUTES.ROUTE_LOGIN}>
                        {t('signup_connection')}
                    </a>
                    <div className="text-center">
                        <a className="regular-text login-lang-change" onClick={() => i18n.changeLanguage(i18n.language === 'fr' ? 'en' : 'fr')}>{t('sidebar_lang')}</a>
                    </div>
                </div>
            </div>
            {modal}
        </div>
    );
}
