import { library } from '@fortawesome/fontawesome-svg-core';
import { faEdit, faFileCirclePlus, faSave, faTrashCan } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import i18n from 'i18next';
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Navigate, useNavigate, useParams } from "react-router-dom";

import Page from '../../components/app/Page';
import { IGroupModel } from '../../models/groupModel';
import { INoteModel, TargetModel } from "../../models/noteModel";
import { IStudentModel, StudentModel } from '../../models/studentModel';
import useNetwork from '../../utils/network/Network';
import * as ROUTES from '../../utils/routes/Routes';
import useToken from "../../utils/useToken";
import CreateNoteModal, { ICreateNoteModalProps } from './modals/CreateNoteModal';

export default function StudentDetails() {
    library.add(faFileCirclePlus, faTrashCan, faEdit, faSave);

    const [student, setStudent] = useState<IStudentModel | undefined>(undefined);
    const [notes, setNotes] = useState<INoteModel[] | undefined>(undefined);
    const { id } = useParams();
    const { t } = useTranslation();
    const { fetchStudent, fetchNotesForStudent, deleteStudent, updateStudent, deleteNote } =
        useNetwork();
    const navigate = useNavigate();
    const ref = React.createRef<ICreateNoteModalProps>();
    const { token } = useToken();
    if (!token) {
        return (
            <>
                <Navigate replace to="/login" />
            </>
        );
    }
    useEffect(() => {
        async function getData() {
            let studentList = await fetchStudent({ id: +id! });
            if (studentList.status === 200) {
                const result = await studentList.json().then((json) => JSON.stringify(json));
                setStudent(new StudentModel(JSON.parse(result)));
            }

            let noteList = await fetchNotesForStudent({ id: +id! });
            if (noteList.status == 200) {
                const result = await noteList.json().then((json) => JSON.stringify(json));
                const json = JSON.parse(result);
                setNotes(json.map((element: INoteModel) => element));
            }
        }
        getData();
    }, [i18n.language]);

    let groupList = student?.group?.map((x: IGroupModel) => {
        return (
            <li key={x.id}>
                <a href={'/group/' + x.id} className="student-seemore-details">
                    {x.name + ' - ' + x.year}
                </a>
            </li>
        );
    });

    notes?.sort(function (a, b) {
        return new Date(b.date).getTime() - new Date(a.date).getTime();
    });
    let noteList = notes?.map((note: INoteModel) => {
        return (
            <React.Fragment key={note.id}>
                <tr onClick={() => ref.current?.openModal(note, { id: +id! })} key={note.id}>
                    <td>{note.title}</td>
                    <td className="table_color_column">
                        <div
                            className="table_color_column_square"
                            style={{ backgroundColor: '#' + note.color }}
                        ></div>
                    </td>
                    <td>
                        {(note.target as TargetModel[])
                            .map((x) => (i18n.language === 'fr' ? x.title_fr : x.title_en))
                            .join(', ')}
                    </td>
                    <td>{i18n.language === 'fr' ? note.type.title_fr : note.type.title_en}</td>
                    <td>{note.date}</td>
                </tr>
            </React.Fragment>
        );
    });

    const handleDelete = async (e: any) => {
        if (window.confirm(t('student_details_deletion'))) {
            notes?.forEach(async (note: INoteModel) => {
                await deleteNote({ id: note.id });
            });

            let noteDeletion = await deleteStudent({ id: +id! });
            if (noteDeletion.status == 204) {
                navigate(ROUTES.ROUTE_STUDENT);
            }
        }
    };

    const handleEdit = async (e: any) => {
        document.getElementById('student-description')?.removeAttribute('disabled');
        document.getElementById('student-description')?.focus();

        document.getElementById('student-details-save')?.setAttribute('style', 'display:unset;');
        document.getElementById('student-details-edit')?.setAttribute('style', 'display:none;');
    };

    const handleSave = async (e: any) => {
        let studentUpdated = await updateStudent(student as StudentModel);
        if (studentUpdated.status === 200) {
            document.getElementById('student-description')?.setAttribute('disabled', '');
            document
                .getElementById('student-details-edit')
                ?.setAttribute('style', 'display:unset;');
            document.getElementById('student-details-save')?.setAttribute('style', 'display:none;');
        }
    };

    const component = (
        <div className="list">
            <h1 className="text-center list-header">
                {student?.first_name +
                    ' ' +
                    student?.last_name +
                    ' - ' +
                    t('year_' + student?.year.toString())}
            </h1>
            <div className="list-container student-details-container">
                <div className="student-details-section-title">
                    {t('student_details_description_title')}
                    <div>
                        <FontAwesomeIcon
                            onClick={handleEdit}
                            className="student-details-delete"
                            id="student-details-edit"
                            icon="edit"
                        />
                        <FontAwesomeIcon
                            onClick={handleSave}
                            className="student-details-delete"
                            id="student-details-save"
                            icon="save"
                        />
                        <FontAwesomeIcon
                            onClick={handleDelete}
                            className="student-details-delete"
                            icon="trash-can"
                        />
                    </div>
                </div>
                <input
                    id="student-description"
                    disabled
                    type="text"
                    className="student-details-description"
                    defaultValue={student?.description}
                    onChange={(e) => {
                        const newStudent = student;
                        newStudent!.description = e.target.value;
                        setStudent(newStudent);
                    }}
                ></input>
                <hr />
                <div className="student-details-section-title">
                    {t('student_details_group_title')}
                </div>
                <ul className="student-details-groups">
                    {groupList?.length ? (
                        groupList
                    ) : (
                        <li key={0}>{t('student_details_no_group')}</li>
                    )}
                </ul>
            </div>
            <div className="list-container student-container--notes">
                <div className="student-notes-header-container">
                    <div className="student-details-section-title">
                        {t('student_details_note_title')}
                    </div>
                    <FontAwesomeIcon
                        onClick={() => ref.current?.openModal(undefined, { id: +id! })}
                        className="student-details-notes-add-icon"
                        icon="file-circle-plus"
                    />
                </div>
                <br />
                <table>
                    <tbody>
                        <tr>
                            <th colSpan={2}>{t('student_details_note_header_title')}</th>
                            <th>{t('student_details_note_header_target')}</th>
                            <th>{t('student_details_note_header_type')}</th>
                            <th>{t('student_details_note_header_date')}</th>
                        </tr>
                        {noteList}
                    </tbody>
                </table>
            </div>
            <CreateNoteModal ref={ref} />
        </div>
    );

    return <Page menu={ROUTES.ROUTE_STUDENT} component={component} />;
}
