import { library } from '@fortawesome/fontawesome-svg-core';
import {
    faCheck,
    faEdit,
    faFileCirclePlus,
    faPlus,
    faSave,
    faTrashCan,
} from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import i18n from 'i18next';
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Navigate, useNavigate, useParams } from "react-router-dom";

import Page from '../../components/app/Page';
import { IEntityRefModel } from '../../models/entityRefModel';
import { GroupModel } from '../../models/groupModel';
import { IStudentModel, StudentModel } from '../../models/studentModel';
import useNetwork from '../../utils/network/Network';
import * as ROUTES from '../../utils/routes/Routes';
import { ROUTE_GROUP } from '../../utils/routes/Routes';
import useToken from "../../utils/useToken";

export default function GroupDetails() {
    const { t } = useTranslation();
    const { fetchGroup, fetchStudents, deleteGroup, updateGroup, updateStudent } = useNetwork();
    const { id } = useParams();
    const [group, setGroup] = useState<GroupModel | undefined>();
    const [students, setStudents] = useState<StudentModel[]>([]);
    const navigate = useNavigate();

    const { token } = useToken();
    if (!token) {
        return (
            <>
                <Navigate replace to="/login" />
            </>
        );
    }

    useEffect(() => {
        async function getData() {
            let groupList = await fetchGroup({ id: +id! });
            if (groupList.status === 200) {
                const result = await groupList.json().then((json) => JSON.stringify(json));
                setGroup(new GroupModel(JSON.parse(result)));
            }

            let studentList = await fetchStudents();
            if (studentList.status === 200) {
                const result = await studentList.json().then((json) => JSON.stringify(json));
                const models = JSON.parse(result).map((e: any) => {
                    return new StudentModel(e);
                });
                setStudents(models);
            }
        }
        getData();
    }, [i18n.language]);
    library.add(faFileCirclePlus, faTrashCan, faEdit, faSave, faPlus, faCheck);

    const handleDelete = async (e: any) => {
        if (window.confirm(t('group_details_deletion'))) {
            let noteDeletion = await deleteGroup({ id: +id! });
            if (noteDeletion.status == 204) {
                navigate(ROUTES.ROUTE_GROUP);
            }
        }
    };

    const handleEdit = async (e: any) => {
        document.getElementById('group-description')?.removeAttribute('disabled');
        document.getElementById('group-description')?.focus();

        document.getElementById('group-details-save')?.setAttribute('style', 'display:unset;');
        document.getElementById('group-details-edit')?.setAttribute('style', 'display:none;');
    };

    const handleSave = async (e: any) => {
        let groupUpdated = await updateGroup(group!);
        if (groupUpdated.status === 200) {
            document.getElementById('group-description')?.setAttribute('disabled', '');
            document.getElementById('group-details-edit')?.setAttribute('style', 'display:unset;');
            document.getElementById('group-details-save')?.setAttribute('style', 'display:none;');
        }
    };

    const handleAddStudent = async (e: any) => {
        setStudents((prevState) => [
            ...prevState,
            new StudentModel({
                first_name: '',
                last_name: '',
                year: 0,
                id: undefined,
            }),
        ]);
    };

    const handleAssociateStudentToGroup = async (e: any) => {
        const studentId = +(document.getElementById('new-student-group') as HTMLSelectElement)
            .value;
        const studentModel = students.filter((student: StudentModel) => {
            return student.id === studentId;
        })[0];

        studentModel.group.push(group!);

        let updatedStudent = await updateStudent(studentModel);
        if (updatedStudent.status === 200) {
            window.location.reload();
        }
    };

    const studentsList = students
        .filter((student: IStudentModel) => {
            return (
                student.id !== undefined &&
                student.group!.filter((group: IEntityRefModel) => {
                    return group.id === +id!;
                }).length === 0
            );
        })
        .map((student: IStudentModel) => {
            return (
                <option key={student.id} value={student.id}>
                    {student.first_name + ' ' + student.last_name}
                </option>
            );
        });

    const studentComponents = students
        .filter((student: IStudentModel) => {
            return (
                student.id === undefined ||
                student.group!.filter((group: IEntityRefModel) => {
                    return group.id === +id!;
                }).length > 0
            );
        })
        .map((student: IStudentModel) => {
            return student.id !== undefined ? (
                <div key={student.id} className="list-link">
                    <a href={ROUTES.ROUTE_STUDENT_DETAIL + student.id}>
                        {student.first_name + ' ' + student.last_name}
                    </a>
                    <hr />
                </div>
            ) : (
                <div key={-1} className="list-link">
                    <div className="group-add-container">
                        <select
                            style={{
                                color: group ? '#'.concat(group.color) : '',
                                borderColor: group ? '#'.concat(group.color) : '',
                            }}
                            id="new-student-group"
                            required
                            defaultValue=""
                            className="input group-input-element"
                        >
                            {studentsList}
                        </select>
                        <FontAwesomeIcon
                            style={{ color: group ? '#'.concat(group.color) : '' }}
                            onClick={handleAssociateStudentToGroup}
                            className="group-details-add"
                            id="group-details-edit"
                            icon="check"
                        />
                    </div>
                </div>
            );
        });

    const component = (
        <div className="list">
            <h1 className="text-center list-header">{group?.name}</h1>
            <div className="list-container group-details-container">
                <div
                    style={{ color: group ? '#'.concat(group.color) : '' }}
                    className="group-details-section-title"
                >
                    {t('group_details_description_title')}
                    <div>
                        <FontAwesomeIcon
                            onClick={handleEdit}
                            className="group-details-delete"
                            id="group-details-edit"
                            icon="edit"
                        />
                        <FontAwesomeIcon
                            onClick={handleSave}
                            className="group-details-delete"
                            id="group-details-save"
                            icon="save"
                        />
                        <FontAwesomeIcon
                            onClick={handleDelete}
                            className="group-details-delete"
                            icon="trash-can"
                        />
                    </div>
                </div>
                <textarea
                    id="group-description"
                    disabled
                    className="group-details-description"
                    defaultValue={group?.description}
                    onChange={(e) => {
                        const newGroup = group;
                        newGroup!.description = e.target.value;
                        setGroup(newGroup);
                    }}
                ></textarea>
            </div>
            <div className="list-container group-container--notes">
                <div
                    className="group-details-section-title"
                    style={{ color: group ? '#'.concat(group.color) : '' }}
                >
                    <h3 className="text-center list-inner-header">{t('group_subtitle')}</h3>
                    <FontAwesomeIcon
                        style={{ color: group ? '#'.concat(group.color) : '' }}
                        onClick={handleAddStudent}
                        className="group-details-delete"
                        icon="plus"
                    />
                </div>
                <div className="list-collapsible">{studentComponents}</div>
            </div>
        </div>
    );

    return (
        <>
            <Page menu={ROUTE_GROUP} component={component} />
        </>
    );
}
