import { library } from '@fortawesome/fontawesome-svg-core';
import { faCalendar, faFontAwesome, faSchool, faUser } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { ForwardedRef, forwardRef, useImperativeHandle, useState } from 'react';
import { useTranslation } from 'react-i18next';
import Modal from 'react-modal';
import { useNavigate } from 'react-router-dom';

import { GroupModel } from '../../../models/groupModel';
import { ISchoolModel } from '../../../models/schoolModel';
import useNetwork from '../../../utils/network/Network';
import * as ROUTES from '../../../utils/routes/Routes';

export interface ICreateGroupModalRefs {
    openModal(): void;
}

export interface ICreateGroupModalProps {
    schools: ISchoolModel[];
}

function CreateGroupModal(props: ICreateGroupModalProps, ref: ForwardedRef<ICreateGroupModalRefs>) {
    const { t } = useTranslation();
    const [modalIsOpen, setIsOpen] = React.useState(false);

    function closeModal() {
        setIsOpen(false);
    }

    useImperativeHandle(ref, () => {
        return {
            openModal() {
                setIsOpen(true);
            },
        };
    });

    const customStyles = {
        content: {
            top: '25%',
            width: '30%',
            height: 'fit-content',
            left: '40%',
        },
    };
    library.add(faUser, faCalendar, faFontAwesome, faSchool);
    Modal.setAppElement('body');

    const [newGroupName, setNewGroupName] = useState<string>();
    const [newGroupYear, setNewGroupYear] = useState<number>();
    const [newGroupSchool, setNewGroupSchool] = useState<number>();
    const [color, setColor] = useState<string>('');
    const { createGroup } = useNetwork();
    const navigate = useNavigate();

    const handleSubmit = async (e: any) => {
        e.preventDefault();

        const group = await createGroup(
            new GroupModel({
                name: newGroupName ?? '',
                year: +newGroupYear!,
                color: color,
                description: '',
                school: { id: newGroupSchool },
            })
        );

        if (group.status === 201) {
            const info = await group.json().then((json) => JSON.stringify(json));
            const groupModel = JSON.parse(info);
            navigate(ROUTES.ROUTE_GROUP_DETAIL + groupModel.id);
        }
    };

    const schoolComponents = props.schools.map((school: ISchoolModel) => {
        return (
            <option key={school.id} value={school.id}>
                {school.name}
            </option>
        );
    });
    return (
        <Modal
            isOpen={modalIsOpen}
            onRequestClose={closeModal}
            contentLabel={t('signup_modal_title')}
            style={customStyles}
        >
            <div className="signup-modal animate__animated animate__backInLeft">
                <h2 className="list-creation-title">{t('group_creation_title')}</h2>
                <form className="auth-form validate-form" onSubmit={handleSubmit}>
                    <div className="wrap-input validate-input">
                        <input
                            required
                            className="input"
                            type="text"
                            onChange={(e) => setNewGroupName(e.target.value)}
                            placeholder={t('group_field_name')}
                        />
                        <span className="focus-input"></span>
                        <span className="symbol-input">
                            <FontAwesomeIcon icon="user" />
                        </span>
                    </div>
                    <div className="wrap-input validate-input">
                        <select
                            required
                            defaultValue=""
                            onChange={(e) => setNewGroupSchool(+e.target.value)}
                            className="input"
                        >
                            <option value="" disabled hidden>
                                {t('group_field_school')}
                            </option>
                            {schoolComponents}
                        </select>
                        <span className="focus-input"></span>
                        <span className="symbol-input">
                            <FontAwesomeIcon icon="school" />
                        </span>
                    </div>
                    <div className="wrap-input validate-input">
                        <input
                            required
                            className="input"
                            type="text"
                            onChange={(e) => setNewGroupYear(+e.target.value)}
                            placeholder={t('group_field_year')}
                        />
                        <span className="focus-input"></span>
                        <span className="symbol-input">
                            <FontAwesomeIcon icon="calendar" />
                        </span>
                    </div>
                    <div className="wrap-input validate-input">
                        <select
                            id="color-selector"
                            required
                            className="input input--small"
                            defaultValue={color}
                            style={color ? { color: '#' + color, borderColor: '#' + color } : {}}
                            onChange={(e) => {
                                setColor(e.target.value);
                                document.getElementById('color-selector')!.style.color =
                                    '#' + e.target.value;
                                document.getElementById('color-selector')!.style.borderColor =
                                    '#' + e.target.value;
                                document.getElementById('color_icon')!.style.color =
                                    '#' + e.target.value;
                            }}
                        >
                            <option disabled key={-1} value="">
                                {t('group_details_note_header_color')}
                            </option>
                            <option key={0} value="ff9999">
                                {t('color_name_red')}
                            </option>
                            <option key={1} value="189ab4">
                                {t('color_name_blue')}
                            </option>
                            <option key={2} value="05445e">
                                {t('color_name_dark')}
                            </option>
                            <option key={3} value="7fbf7f">
                                {t('color_name_green')}
                            </option>
                        </select>
                        <span className="focus-input"></span>
                        <span className="symbol-input">
                            <FontAwesomeIcon
                                style={color ? { color: '#' + color } : {}}
                                id="color_icon"
                                icon="font-awesome"
                            />
                        </span>
                    </div>
                    <br />
                    <button className="auth-form-btn">{t('group_submit')}</button>
                </form>
            </div>
        </Modal>
    );
}

export default forwardRef(CreateGroupModal);
