import { IEntityRefModel } from '../../models/entityRefModel';
import { GroupModel } from '../../models/groupModel';
import { NoteModel } from '../../models/noteModel';
import { StudentModel } from '../../models/studentModel';
import useToken from '../useToken';
import * as Urls from './Urls';

export default function useNetwork() {
    const { token } = useToken();

    const getAuthHeader = () => {
        let headers = new Headers();
        headers.append('Authorization', 'Bearer ' + token);
        headers.append('Accept', 'application/json');
        headers.append('Content-Type', 'application/json');
        return headers;
    };

    const genericFetch = async (route: string) => {
        return fetch(route, {
            method: 'GET',
            headers: getAuthHeader(),
        }).then((data) => {
            return data;
        });
    };

    const loginUser = async (credentials: any) => {
        const body = new FormData();
        body.append('username', credentials.username);
        body.append('password', credentials.password);

        return fetch(Urls.LOGIN_URL, {
            method: 'POST',
            body: body,
        }).then((data) => {
            if (data.status !== 200) {
                return null;
            }
            return data.json();
        });
    };

    const createUser = async (credentials: any) => {
        const body = new FormData();
        body.append('username', credentials.username);
        body.append('email', credentials.email);
        body.append('first_name', credentials.firstName);
        body.append('last_name', credentials.lastName);
        body.append('password', credentials.password);

        return fetch(Urls.CREATE_ACCOUNT_URL, {
            method: 'POST',
            body: body,
        }).then((data) => {
            return data;
        });
    };
    const fetchSchools = async () => {
        return genericFetch(Urls.FETCH_SCHOOLS);
    };
    const fetchGroups = async () => {
        return genericFetch(Urls.GROUPS);
    };

    const fetchStudents = async (group?: number) => {
        return genericFetch(group ? Urls.STUDENTS + '?group=' + group : Urls.STUDENTS);
    };
    const fetchStudent = async (entity: IEntityRefModel) => {
        return genericFetch(Urls.STUDENTS + entity.id + '/');
    };
    const fetchGroup = async (entity: IEntityRefModel) => {
        return genericFetch(Urls.GROUPS + entity.id + '/');
    };

    const updateStudent = async (student: StudentModel) => {
        return fetch(Urls.STUDENTS + student.id + '/', {
            method: 'PUT',
            headers: getAuthHeader(),
            body: student.toJson(),
        }).then((data) => {
            return data;
        });
    };

    const fetchNotes = async () => {
        return genericFetch(Urls.NOTES);
    };

    const fetchNoteTypes = async () => {
        return genericFetch(Urls.FETCH_NOTE_TYPES);
    };

    const fetchTargets = async () => {
        return genericFetch(Urls.FETCH_TARGETS);
    };

    const fetchNotesForStudent = async (entity: IEntityRefModel) => {
        return genericFetch(Urls.NOTES + '?student=' + entity.id);
    };

    const createStudent = async (params: StudentModel) => {
        return fetch(Urls.STUDENTS, {
            method: 'POST',
            headers: getAuthHeader(),
            body: params.toJson(),
        }).then((data) => {
            return data;
        });
    };

    const updateNote = async (note: NoteModel) => {
        return fetch(Urls.NOTES + note.id + '/', {
            method: 'PUT',
            headers: getAuthHeader(),
            body: note.toJson(),
        }).then((data) => {
            return data;
        });
    };

    const updateGroup = async (group: GroupModel) => {
        return fetch(Urls.GROUPS + group.id + '/', {
            method: 'PUT',
            headers: getAuthHeader(),
            body: group.toJson(),
        }).then((data) => {
            return data;
        });
    };

    const deleteNote = async (entity: IEntityRefModel) => {
        return fetch(Urls.NOTES + entity.id?.toString() + '/', {
            method: 'DELETE',
            headers: getAuthHeader(),
        }).then((data) => {
            return data;
        });
    };

    const deleteStudent = async (entity: IEntityRefModel) => {
        return fetch(Urls.STUDENTS + entity.id?.toString() + '/', {
            method: 'DELETE',
            headers: getAuthHeader(),
        }).then((data) => {
            return data;
        });
    };

    const deleteGroup = async (entity: IEntityRefModel) => {
        return fetch(Urls.GROUPS + entity.id?.toString() + '/', {
            method: 'DELETE',
            headers: getAuthHeader(),
        }).then((data) => {
            return data;
        });
    };

    const createNote = async (note: NoteModel) => {
        return fetch(Urls.NOTES, {
            method: 'POST',
            headers: getAuthHeader(),
            body: note.toJson(),
        }).then((data) => {
            return data;
        });
    };

    const createGroup = async (group: GroupModel) => {
        return fetch(Urls.GROUPS, {
            method: 'POST',
            headers: getAuthHeader(),
            body: group.toJson(),
        }).then((data) => {
            return data;
        });
    };

    return {
        // Auth
        createUser,
        loginUser,

        // GET
        fetchGroups,
        fetchGroup,
        fetchStudents,
        fetchStudent,
        fetchNoteTypes,
        fetchTargets,
        fetchNotes,
        fetchNotesForStudent,
        fetchSchools,

        // POST
        createStudent,
        createNote,
        createGroup,

        // PUT
        updateStudent,
        updateNote,
        updateGroup,

        // DELETE
        deleteStudent,
        deleteNote,
        deleteGroup,
    };
}
