import { library } from '@fortawesome/fontawesome-svg-core';
import { faLock, faUserGraduate } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import i18n from 'i18next';
import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Navigate, useNavigate } from 'react-router-dom';

import { ReactComponent as LogoSvg } from '../../logo.svg';
import network from '../../utils/network/Network';
import * as ROUTES from '../../utils/routes/Routes';
import useToken from '../../utils/useToken';

export default function Login() {
    const [username, setUserName] = useState<string>();
    const [password, setPassword] = useState<string>();
    const { loginUser } = network();
    const navigate = useNavigate();
    const { t } = useTranslation();
    const { token, setToken } = useToken();

    if (token) {
        return <Navigate replace to={ROUTES.ROUTE_DASHBOARD} />;
    }

    const handleSubmit = async (e: any) => {
        e.preventDefault();
        const token = await loginUser({
            username,
            password,
        });
        if (token) {
            setToken(token);
            navigate(ROUTES.ROUTE_DASHBOARD);
        } else {
            (document.getElementById('auth-error') as HTMLDivElement).style.display = 'block';
        }
    };

    library.add(faUserGraduate, faLock);
    return (
        <div className="container-auth">
            <div className="wrap-auth">
                <div className="auth-pic">
                    <LogoSvg />
                </div>
                <form className="auth-form validate-form" onSubmit={handleSubmit}>
                    <span className="auth-form-title ">{t('login_title')}</span>
                    <div className="wrap-input validate-input">
                        <input
                            className="input"
                            type="text"
                            onChange={(e) => setUserName(e.target.value)}
                            placeholder={t('login_username_placeholder')}
                        />
                        <span className="focus-input"></span>
                        <span className="symbol-input">
                            <FontAwesomeIcon icon="user-graduate" />
                        </span>
                    </div>
                    <div className="wrap-input validate-input">
                        <input
                            className="input"
                            type="password"
                            onChange={(e) => setPassword(e.target.value)}
                            placeholder={t('login_password_placeholder')}
                        />
                        <span className="focus-input"></span>
                        <span className="symbol-input">
                            <FontAwesomeIcon icon="lock" />
                        </span>
                    </div>
                    <div className="text-center regular-text " id="auth-error">
                        {t('login_error_generic')}
                    </div>
                    <div className="container-auth-form-btn">
                        <button className="auth-form-btn">{t('login_connexion')}</button>
                    </div>
                    <div className="text-center auth-subtitle">
                        <a className="regular-text login-forget-password">{t('login_lost_username')}</a>
                        <span className="light-text">{t('login_lost_label')}</span>
                    </div>
                    <div className="text-center login-signup">
                        <a className="regular-text" href={ROUTES.ROUTE_SIGNUP}>
                            {t('login_account_creation')}
                        </a>
                        <div className="text-center">
                            <a className="regular-text login-lang-change" onClick={() => i18n.changeLanguage(i18n.language === 'fr' ? 'en' : 'fr')}>{t('sidebar_lang')}</a>
                        </div>
                    </div>
                </form>
            </div>
        </div>
    );
}
